'use client';

import { useState, useTransition } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { format } from 'date-fns';
import { CalendarIcon, Loader2 } from 'lucide-react';
import { useRouter } from 'next/navigation';
import { toast } from 'sonner';

import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from '@/components/ui/popover';
import { Calendar } from '@/components/ui/calendar';
import { Checkbox } from '@/components/ui/checkbox';

import { createCaregiver, updateCaregiver } from '../actions';
import {
  caregiverFormSchema,
  type CaregiverFormValues,
  defaultCaregiverValues,
  GenderEnum,
  EducationEnum,
  WorkExperienceLevelEnum,
  LiveInStatusEnum,
} from '../schema';

// ... (existing constants)
const STEPS = [
  { id: 'basic', title: '' },
  { id: 'professional', title: '' },
  { id: 'files', title: '' },
];

const SPECIALTIES_OPTIONS = ["", "", "", "", "", "", ""];

// ... (existing maps)
const EDUCATION_MAP: Record<string, string> = {
  PRIMARY: '',
  JUNIOR_HIGH: '',
  SENIOR_HIGH: '',
  VOCATIONAL: '/',
  COLLEGE: '',
  BACHELOR: '',
};

const EXPERIENCE_LEVEL_MAP: Record<string, string> = {
  ENTRY: ' (1)',
  INTERMEDIATE: ' (1-3)',
  SENIOR: ' (3-5)',
  EXPERT: ' (5)',
};

interface CaregiverFormProps {
  initialData?: CaregiverFormValues & { idString: string };
}

export function CaregiverForm({ initialData }: CaregiverFormProps) {
  const [currentStep, setCurrentStep] = useState(0);
  const [isPending, startTransition] = useTransition();
  const router = useRouter();
  const isEdit = !!initialData;

  const form = useForm<CaregiverFormValues>({
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    resolver: zodResolver(caregiverFormSchema) as any,
    defaultValues: initialData || defaultCaregiverValues as CaregiverFormValues,
    mode: 'onChange',
  });

  const { trigger } = form;

  // Handle Next Step with Validation
  const handleNext = async () => {
    let fieldsToValidate: (keyof CaregiverFormValues)[] = [];

    if (currentStep === 0) {
      fieldsToValidate = [
        'workerId',
        'name',
        'phone',
        'idCardNumber',
        'dob',
        'gender',
        'nativePlace',
        'education',
        'notes',
      ];
    } else if (currentStep === 1) {
      fieldsToValidate = [
        'workExpLevel',
        'isLiveIn',
        'specialties',
      ];
    }

    const isStepValid = await trigger(fieldsToValidate);

    if (isStepValid) {
      setCurrentStep((prev) => Math.min(prev + 1, STEPS.length - 1));
    }
  };

  const handlePrevious = () => {
    setCurrentStep((prev) => Math.max(prev - 1, 0));
  };

  const onSubmit = (data: CaregiverFormValues) => {
    startTransition(async () => {
      let result;
      
      if (isEdit && initialData) {
        result = await updateCaregiver(initialData.idString, data);
      } else {
        result = await createCaregiver(data);
      }

      if (result.success) {
        toast.success(result.message);
        router.push('/caregivers'); 
        router.refresh();
      } else {
        toast.error(result.message || '');
      }
    });
  };

  return (
    <Card className="w-full max-w-2xl mx-auto">
      <CardHeader>
        <CardTitle>{isEdit ? '' : ''}</CardTitle>
        <CardDescription>
           {currentStep + 1} / {STEPS.length}: {STEPS[currentStep].title}
        </CardDescription>
      </CardHeader>
      
      <CardContent>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
            
            {/* --- Step 1: Basic Info --- */}
            <div className={cn(currentStep === 0 ? "block" : "hidden", "space-y-4")}>
              <div className="grid gap-4 md:grid-cols-2">
                <FormField
                  control={form.control}
                  name="workerId"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <FormControl>
                        <Input placeholder="" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="name"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <FormControl>
                        <Input placeholder="" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="phone"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <FormControl>
                        <Input placeholder="11" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="idCardNumber"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <FormControl>
                        <Input placeholder="18" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="dob"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <FormControl>
                        <Input 
                          type="date"
                          max={new Date().toISOString().split('T')[0]}
                          {...field}
                          value={field.value instanceof Date ? format(field.value, 'yyyy-MM-dd') : (field.value ?? '')}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="gender"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <Select onValueChange={field.onChange} defaultValue={field.value ?? undefined}>
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue placeholder="" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          {GenderEnum.options.map((gender) => (
                            <SelectItem key={gender} value={gender}>
                              {gender === 'MALE' ? '' : ''}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="nativePlace"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <FormControl>
                        <Input placeholder="" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="education"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <Select onValueChange={field.onChange} defaultValue={field.value ?? undefined}>
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue placeholder="" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          {EducationEnum.options.map((edu) => (
                            <SelectItem key={edu} value={edu}>
                              {EDUCATION_MAP[edu] || edu}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              <FormField
                control={form.control}
                name="notes"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>/</FormLabel>
                    <FormControl>
                      <Textarea
                        placeholder="..."
                        className="resize-none"
                        {...field}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            {/* --- Step 2: Professional Info --- */}
            <div className={cn(currentStep === 1 ? "block" : "hidden", "space-y-4")}>
               <div className="grid gap-4 md:grid-cols-2">
                <FormField
                  control={form.control}
                  name="workExpLevel"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <Select onValueChange={field.onChange} defaultValue={field.value ?? undefined}>
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue placeholder="" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          {WorkExperienceLevelEnum.options.map((level) => (
                            <SelectItem key={level} value={level}>
                              {EXPERIENCE_LEVEL_MAP[level] || level}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="isLiveIn"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <Select onValueChange={field.onChange} defaultValue={field.value ?? undefined}>
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue placeholder="" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          {LiveInStatusEnum.options.map((status) => (
                            <SelectItem key={status} value={status}>
                              {status === 'LIVE_IN' ? '' : status === 'LIVE_OUT' ? '' : ''}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              <FormField
                control={form.control}
                name="specialties"
                render={() => (
                  <FormItem>
                    <div className="mb-4">
                      <FormLabel className="text-base"> ()</FormLabel>
                      <FormDescription>
                        
                      </FormDescription>
                    </div>
                    <div className="grid grid-cols-2 gap-4 md:grid-cols-3">
                      {SPECIALTIES_OPTIONS.map((item) => (
                        <FormField
                          key={item}
                          control={form.control}
                          name="specialties"
                          render={({ field }) => {
                            return (
                              <FormItem
                                key={item}
                                className="flex flex-row items-start space-x-3 space-y-0"
                              >
                                <FormControl>
                                  <Checkbox
                                    checked={field.value?.includes(item)}
                                    onCheckedChange={(checked) => {
                                      return checked
                                        ? field.onChange([...field.value, item])
                                        : field.onChange(
                                            field.value?.filter(
                                              (value) => value !== item
                                            )
                                          )
                                    }}
                                  />
                                </FormControl>
                                <FormLabel className="font-normal cursor-pointer">
                                  {item}
                                </FormLabel>
                              </FormItem>
                            )
                          }}
                        />
                      ))}
                    </div>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            {/* --- Step 3: Files --- */}
            <div className={cn(currentStep === 2 ? "block" : "hidden", "space-y-4")}>
              <FormField
                control={form.control}
                name="avatarUrl"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel> URL ()</FormLabel>
                    <FormControl>
                      <Input placeholder=" (Mock)" {...field} />
                    </FormControl>
                    <FormDescription></FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <div className="grid gap-4 md:grid-cols-2">
                <FormField
                  control={form.control}
                  name="idCardFrontUrl"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel> URL</FormLabel>
                      <FormControl>
                        <Input placeholder=" (Mock)" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="idCardBackUrl"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel> URL</FormLabel>
                      <FormControl>
                        <Input placeholder=" (Mock)" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>
            </div>

            {/* --- Navigation Buttons --- */}
            <div className="flex justify-between pt-6 border-t">
              <Button
                type="button"
                variant="outline"
                onClick={handlePrevious}
                disabled={currentStep === 0 || isPending}
                className={cn(currentStep === 0 && 'invisible')}
              >
                
              </Button>

              {currentStep < STEPS.length - 1 ? (
                <Button type="button" onClick={handleNext}>
                  
                </Button>
              ) : (
                <Button type="submit" disabled={isPending}>
                  {isPending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                  {isPending ? '...' : (isEdit ? '' : '')}
                </Button>
              )}
            </div>
          </form>
        </Form>
      </CardContent>
    </Card>
  );
}
